:root {
  --color-button-secondary-background: transparent;
  --color-button-secondary-hover-background: #e7ebf0;
  --color-button-secondary-text: #0071c4;
  --color-button-secondary-hover-text: #0071c4;
  --color-button-primary-background: #008A00;
  --color-button-primary-hover-background: #006400;
  --color-button-primary-text: #fff;
  --color-button-primary-hover-text: #fff;
  --color-field-background: #fff;
  --color-field-border: #ced5e1;
  --color-field-text: #303b4d;
  --color-form-background: #fff;
  --color-form-text: #43536d;
  --color-link-primary: #0071c4;
  --color-message-error: #71141f;
  --color-message-warning: #856404;
  --color-page-background: #f7f8fa;
  --color-page-text: #303b4d;
  --border-radius-form: 4px;
  --border-radius-field: 3px;
  --box-shadow-form: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  --sizing-app-brand-max-height: 150px;
  --sizing-app-brand-max-width: 300px;
  --sizing-content-width: 330px;
  --spacing-app-brand: 30px;
  --spacing-field-content: 8px;
  --spacing-form: 30px 20px 40px;
  --spacing-form-desktop: 30px 30px 40px;
  --spacing-form-controls: 20px;
  --spacing-form-label-bottom: 5px;
  --spacing-silverstripe-brand: 30px;
  --spacing-submit-content: 10px 20px;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-button-primary-background: #F6C445;
    --color-button-primary-hover-background: #E0A800;
    --color-button-primary-text: #3E464E;
    --color-button-primary-hover-text: #3E464E;
    --color-button-secondary-background: transparent;
    --color-button-secondary-hover-background: #5a656f;
    --color-button-secondary-text: #ced5e1;
    --color-button-secondary-hover-text: #ced5e1;
    --color-field-background: #4F5861;
    --color-field-border: #4F5861;
    --color-field-text: #fff;
    --color-form-background: #3E464E;
    --color-form-text: #fff;
    --color-link-primary: #ced5e1;
    --color-message-error: #DA273B;
    --color-message-warning: #FFA500;
    --color-page-background: #4F5861;
    --color-page-text: #fff;
  }
}

a {
  color: var(--color-link-primary);
  text-decoration: none;
}

a:hover {
  color: var(--color-link-primary);
  text-decoration: underline;
}

a.link--secondary,
a.link--secondary:hover {
  color: var(--color-form-text);
}

h2 {
  font-size: 1.5rem;
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  display: grid;
  grid-template-columns: 1fr minmax(320px, max-content) 1fr;
  grid-template-rows: 1fr auto auto 80px 1fr;
  height: 100vh;
}

.app-brand {
  grid-column: 2;
  grid-row: 2;
}

.login-form {
  grid-column: 2;
  grid-row: 3;
  display: block;
}

.silverstripe-brand {
  grid-column: 2;
  grid-row: 4;
}

.app-brand {
  display: flex;
  justify-content: center;
  padding: var(--spacing-app-brand);
}

.app-brand__link,
.app-brand__link:hover {
  color: var(--color-page-text);
  text-decoration: none;
}

.app-brand__logo {
  max-width: var(--sizing-app-brand-max-width);
  max-height: var(--sizing-app-brand-max-height);
}

.app-brand__logo[src*=".svg"] {
  width: 100%;
}

.app-brand__name {
  margin: 0;
}

.app-brand__logo + .app-brand__name {
  display: none;
}

.app-brand__logo--dark-mode {
  display: none;
}

@media (prefers-color-scheme: dark) {
  .app-brand__logo--light-mode {
    display: none;
  }

  .app-brand__logo--dark-mode {
    display: block;
  }
}

.silverstripe-brand {
  color: var(--color-page-text);
  display: flex;
  justify-content: center;
  padding: var(--spacing-silverstripe-brand);
}

html {
  background-color: var(--color-page-background);
  color: var(--color-page-text);
}

html body {
  background-color: inherit;
}

.login-form {
  background-color: var(--color-form-background);
  box-shadow: var(--box-shadow-form);
  color: var(--color-form-text);
  padding: var(--spacing-form);
  width: 100vw;
}

@media (min-width: 420px) {
  .login-form {
    border-radius: var(--border-radius-form);
    padding: var(--spacing-form-desktop);
    width: auto;
  }
}

.login-form .login-form-control--text .login-form-control__field,
.login-form input.text,
.login-form input.text:focus {
  background-color: var(--color-field-background);
  border: 1px solid var(--color-field-border);
  border-radius: var(--border-radius-field);
  color: var(--color-field-text);
  padding: var(--spacing-field-content);
  width: 100%;
}

@media (min-width: 420px) {
  .login-form .login-form-control--text .login-form-control__field,
  .login-form input.text,
  .login-form input.text:focus {
    width: var(--sizing-content-width);
  }
}

.login-form__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login-form__header a {
  font-size: 19px;
  color: white;
}

.login-form__header a:hover {
  text-decoration: none;
}

.login-form__title {
  margin-top: 0;
}

.login-form__content,
.login-form__message,
.message {
  max-width: var(--sizing-content-width);
}

.login-form__message--warning,
.message.warning {
  color: var(--color-message-warning);
}

.login-form__message--error,
.message.bad,
.message.error,
.message.required {
  color: var(--color-message-error);
}

.login-form__fields,
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

.login-form-control,
.field {
  margin-bottom: var(--spacing-form-controls);
}

.login-form-control--text .login-form-control__label,
.field.text label {
  display: block;
  margin-bottom: var(--spacing-form-label-bottom);
}

.login-form-actions,
.btn-toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.action {
  background-color: var(--color-button-secondary-background);
  border: 1px solid currentColor;
  border-radius: var(--border-radius-field);
  color: var(--color-button-secondary-text);
  cursor: pointer;
  padding: var(--spacing-submit-content);
}

.btn {
  border-radius: var(--border-radius-field);
  padding: var(--spacing-submit-content);
}

